.contacts {
  padding: 0 14%;
  background-color: #323941;
  height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  :nth-child(6){
    margin-left: 80px;
  }
  img {
    height: 25px;
    width: 25px;
    margin: 13px 0;
  }
  a {
    color: white;
    text-decoration: none;
  }
}
.logo{
  padding: 0 14%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img{
    height: 63px;
    margin: 20px 0;
  }
}
.manager {
  display: flex;
  flex-wrap: nowrap;
  width: 200px;
  font-weight: 600;
  font-size: 13px;
}
.ask {
  padding: 25px 0 0 13px;
  a {
    color: #009EFF;
  }
}