.presentation {
  background-color: #323941;
  padding: 5px 14%;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  a:hover{
    background-color: #566270;
  }
  a{
    text-decoration: none;
    color: darkgrey;
  }

  .card {
    width: 125px;
    height: 110px;
    font-size: 18px;
    margin: 5px 0;
    text-align: center;
    padding: 5px 5px;
    h4{
      color: white;
    }

    img {
      width: 80px;
      height: 80px;

    }
  }
}
.action {
  height: 48px;
  background: #009EFF;
  color: white;
  text-align: center;
  line-height: 48px;
}
.prod_show {
  img{
    height: 600px;
    width: 500px;
  }
}